import React from 'react'
import Router from 'next/router'
import { Icon, Label, Menu, Table, Input } from 'semantic-ui-react'
import axios from 'axios'
import Link from 'next/link'

export default class extends React.Component { 
  constructor (props) {
    super(props)
    this.state = { 
      loading: false, 
    }
    this.requestMccoin = this.requestMccoin.bind(this);
    
  }

  // handling escape close
  componentDidMount () {
   //this.requestMccoin();
  }

  componentWillUnmount () {
   
  }

  async requestMccoin() {
      //9383deb8decad89faec764db2ab881358304b022
      console.log(this.state.address);
      this.setState({
        loading: true,
        response: null,
      })
      const response = await axios.get('http://localhost:3001/faucet/'+this.state.address+'/10');
      console.log(response.data);
      if(response){
        this.setState({
          loading: false,
          response: response.data,
          show: false,
          render: true,
        })
        if(response.data === 'Request accepted!'){
          this.setState({
            positive: true,
          })
        }
        else{
          this.setState({
            negative: true,
          })
        }
        setTimeout(function() { //Start the timer
            this.setState({render: false}) //After 1 second, set render to true
        }.bind(this), 5000)
      }
  }


  render () {

    return (
      <div>
        <link rel="stylesheet" href="https://stackpath.bootstrapcdn.com/bootstrap/4.1.0/css/bootstrap.min.css" integrity="sha384-9gVQ4dYFwwWSjIDZnLEWnxCjeSWFphJiwGPXr1jddIhOegiu1FwO5qRGvFXOdJZ4" crossOrigin="anonymous"/>
		    <link rel="stylesheet" href="//cdnjs.cloudflare.com/ajax/libs/semantic-ui/2.2.12/semantic.min.css"></link>
        <link rel="stylesheet" href="https://use.fontawesome.com/releases/v5.0.13/css/all.css" integrity="sha384-DNOHZ68U8hZfKXOrtjWvjxusGo9WQnrNx2sqG0tfsghAvtVlRW3tvkXWZh58N9jp" crossOrigin="anonymous"/>
        <link href="https://fonts.googleapis.com/css?family=Baloo+Chettan|Paytone+One" rel="stylesheet"/>
        <div className="container" style={{'max-width': '97%'}}>
        <div class="row" style={{'background-color': 'red'}}>
          <div class="col-sm-6" style={{height:'100px'}}>
          <span style={{'font-family': 'Paytone One', 'font-size': '50px', 'margin-top': '70px'}}>Mccoin Faucet</span>
          </div>
          <div class="col-sm-6">
            <Input fluid placeholder='Search...' />
          </div>
        </div>
        <Table celled>
        <Table.Header>
          <Table.Row>
            <Table.HeaderCell>Header</Table.HeaderCell>
            <Table.HeaderCell>Header</Table.HeaderCell>
            <Table.HeaderCell>Header</Table.HeaderCell>
          </Table.Row>
        </Table.Header>

        <Table.Body>
          <Table.Row>
            <Table.Cell>
              <Label ribbon>First</Label>
            </Table.Cell>
            <Table.Cell>Cell</Table.Cell>
            <Table.Cell>Cell</Table.Cell>
          </Table.Row>
          <Table.Row>
            <Table.Cell>Cell</Table.Cell>
            <Table.Cell>Cell</Table.Cell>
            <Table.Cell>Cell</Table.Cell>
          </Table.Row>
          <Table.Row>
            <Table.Cell>Cell</Table.Cell>
            <Table.Cell>Cell</Table.Cell>
            <Table.Cell>Cell</Table.Cell>
          </Table.Row>
        </Table.Body>

        <Table.Footer>
          <Table.Row>
            <Table.HeaderCell colSpan='3'>
              <Menu floated='right' pagination>
                <Menu.Item as='a' icon>
                  <Icon name='chevron left' />
                </Menu.Item>
                <Menu.Item as='a'>1</Menu.Item>
                <Menu.Item as='a'>2</Menu.Item>
                <Menu.Item as='a' icon>
                  <Icon name='chevron right' />
                </Menu.Item>
              </Menu>
            </Table.HeaderCell>
          </Table.Row>
        </Table.Footer>
      </Table>
        </div>
      </div>
    )
  }
}


